<!--
 * @Description: 抢货单详情
 * @Version: 1.0.0
 * @Autor: gitxinghui
 * @Date: 2020-12-05 02:04:42
 * @LastEditors: Please set LastEditors
 * @LastEditTime: 2021-01-15 03:39:44
-->
<template>
    <view class="order_freight_info">
        <!-- tabbar -->
        <cu-custom :refresh="preRrefresh"><block slot="content">货单详情</block></cu-custom>
        <scroll-view class="order_freight_info_scroll" :class="(orderInfo.user_release_goods.status >= 1 && orderInfo.user_release_goods.status < 5) || orderInfo.user_release_goods.status === 6 || orderInfo.user_release_goods.status === 8 ? 'btn-padding-bottom':''" scroll-y="true">
            <!-- 订单状态 -->
            <view class="flex justify-center align-start order_type_info">
                <view class="type_text">{{orderInfo.user_release_goods.status | grabFreightorder_format}}</view>
                <view class="type_tip_text">{{orderInfo.user_release_goods.status | grabFreightOrderTip_format}}</view>
            </view>
            <!-- 货车信息 -->
            <view class="things_width_full order_info">
                <view class="order_single_info">司机：{{orderInfo.realname}}</view>
                <view class="order_single_info">押运员：{{orderInfo.escort_name}}</view>
                <view class="order_single_info">车牌号：{{orderInfo.car_number}}</view>
                <view class="order_single_info">挂车牌号：{{orderInfo.listing_car_number}}</view>
            </view>
            <!-- 货物信息 -->
            <view class="things_width_full freight_info">
                <!-- 地址 -->
                <view class="flex justify-between align-center address_info">
                    <view class="address_info_left">
                        <view class="text-cut start_place">装车地点：{{orderInfo.user_release_goods.releaseLoadingAddress}}</view>
                        <view class="text-cut arrive_place">卸车地点：{{orderInfo.user_release_goods.releaseUnloadingAddress}}</view>
                    </view>
                    <view class="address_info_right" @tap="linkToGps">
                        <image class="gps_icon" src="./../../../../../../static/images/global/gps_icon.png" mode="widthFix" />
                    </view>
                </view>
                <view class="freight_basic_info">
                    <view class="order_num">订单编号：{{orderInfo.user_release_goods.order_no}}</view>
                    <view class="things_width_full flex justify-start align-center user_info">
                        <view class="imageBox">
                            <image class="user_cover" :src="orderInfo.user_release_goods.users.avatarUrl" mode="widthFix"/>
                        </view>
                        <view class="name">{{orderInfo.user_release_goods.users.nickname}}</view>
                        <view class="flex justify-start align-center score">
                            <image class="score_icon" src="./../../../../../../static/images/global/score_icon.png" mode="widthFix" />
                            <view class="score_num">{{orderInfo.user_release_goods.users.reputation}}</view>
                            <view class="identity_text">{{orderInfo.user_release_goods.users.user_type}}</view>
                        </view>
                    </view>
                    <view class="freight_single_info">运输总价格：￥{{orderInfo.user_release_goods.total_money}}</view>
                    <view class="freight_single_info">货物名称：{{orderInfo.user_release_goods.goods_name}}</view>
                    <view class="freight_single_info">货物类型：{{orderInfo.user_release_goods.goods_type}}</view>
                    <view class="freight_single_info">损耗范围：{{orderInfo.user_release_goods.loss_range}}</view>
                    <view class="freight_single_info">重量：{{orderInfo.user_release_goods.weight}}吨</view>
                    <view class="freight_single_info">货物介质：{{orderInfo.user_release_goods.goods_medium}}</view>
                    <view class="freight_single_info">开始时间：{{orderInfo.user_release_goods.start_time}}</view>
                    <view class="freight_single_info">结束时间：{{orderInfo.user_release_goods.end_time}}</view>
                    <view class="freight_single_info">下单时间：{{orderInfo.user_release_goods.create_time}}</view>
                    <view class="freight_single_info">备注：{{orderInfo.user_release_goods.remark}}</view>
                    <!-- 货车上传到达的单据 -->
                    <view class="pay_msg" v-if="orderInfo.user_release_goods.status >= 9">
                        <view class="pay_title">收货单据</view>
                        <view class="car_single_info">损耗：{{orderInfo.user_release_goods.loss_weight}}</view>
                        <view class="things_width_full receipt_list">
                            <image class="receipt_item" :src="orderInfo.user_release_goods.transport_pic.pic_path" :data-url="orderInfo.user_release_cars.transport_pic.pic_path" mode="widthFix" @tap="previewImage" />
                        </view>
                    </view>
                </view>
            </view>
        </scroll-view>
        <!-- 按钮操作区域 -->
        <view class="things_width_full flex justify-end align-center btn_box" v-if="(orderInfo.user_release_goods.status >= 1 && orderInfo.user_release_goods.status < 5) || orderInfo.user_release_goods.status === 6 || orderInfo.user_release_goods.status === 8">
            <!-- 取消订单 -->
            <button class="bg-white cu-btn cancle_btn" :disabled="buttonType" v-if="orderInfo.user_release_goods.status >= 1 && orderInfo.user_release_goods.status < 5" :data-id="orderInfo.id" @tap="cancelOrderFun">取消订单</button>
            <!-- 确认运输 -->
            <button class="bg-white cu-btn submit_btn" :disabled="buttonType" v-if="orderInfo.user_release_goods.status === 6" :data-id="orderInfo.id" @tap="confirmTransportFun">开始运输</button>
            <!-- 完成运输 -->
            <button class="bg-white cu-btn submit_btn" :disabled="buttonType" v-if="orderInfo.user_release_goods.status === 8" :data-id="orderInfo.id" @tap="linkToUploadReceipt">完成运输</button>
        </view>
        <!-- loading弹框组件 -->
		<popLinkDialog></popLinkDialog>
		<popLoading></popLoading>
		<popDialog></popDialog>
		<tipModal></tipModal>
		<!-- 登录 -->
		<login></login>
    </view>
</template>

<script>
import { getGrabsGoodsOrderDetail_api,cancelOrder_api,confirmTransactionGrabsGoods_api } from '@/api/index.js';
	import { appData } from '@/utils/config.js';
	import { mapState, mapGetters, mapActions, mapMutations } from 'vuex';
	import storage from '@/utils/storage.js';
    import { identity_format } from '@/utils/filters.js';
    import checkForm from '@/utils/checkFormData.js';
    import { grabFreightorder_format,grabFreightOrderTip_format } from '@/utils/filters.js';
	let that = null;
export default {
    data() {
        return {
            appBaseData:null,//全局配置信息
            userInfo: {},
            order_id: '',
            orderInfo: {
                user_release_goods: {}
            },// 订单详情
            buttonType: false,
            preRrefresh: false,// 判断上一页是否要刷新
        }
    },
    watch: {
        '$store.getters.user_token': function(val) {
            that.getFreightOrderInfo();
        },
    },
    // 过滤器
    filters: {
        // 订单状态
        grabFreightorder_format,
        grabFreightOrderTip_format
    },
    computed: {
        ...mapGetters(['base_cancelOrderType']) 
    },
    methods: {
        ...mapMutations([
            'modal',
            'base'
        ]),
        /**清空数据
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        clearPageFun(e) {
            that.getFreightOrderInfo();
        },
        /**跳转到gps
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        linkToGps(e) {
            // uni.navigateTo({
            //     url: `/pages/things/user/order/gps/index`
            // });
        },
        /**取消订单
         * @description: 
         * @param {*} e
         * @return {*}
         */
        cancelOrderFun(e) {
            that.buttonType = true;
            let params = {
                order_id: e.currentTarget.dataset.id,
                type: that.base_cancelOrderType[1].value,
            };
            cancelOrder_api(params,that).then(res=>{
                that.modal({name:'popLinkDialog',value:{type:true,tipTitle:'提示',tipContent:'取消成功',linkUrl:1}});
                that.base({name:'refreshPage',value:true});
                that.buttonType = false;
            }).catch(err=>{
                console.log(err);
                that.buttonType = false;
            })
        },
        /**开始运输
         * @description: 
         * @param {*} e
         * @return {*}
         */
        confirmTransportFun(e) {
            that.buttonType = true;
            let params = {
                grabs_order_id: e.currentTarget.dataset.id,
            };
            confirmTransactionGrabsGoods_api(params,that).then(res=>{
                that.modal({name:'popTipModal',value:{type:true,tipTitle:'提示',tipContent:'请开始运输货物'}});
                that.preRrefresh = true;
                that.clearPageFun();
                that.buttonType = false;
            }).catch(err=>{
                console.log(err);
                that.buttonType = false;
            })
        },
        /**跳转到支付
         * @description: 
         * @param {*} e
         * @return {*}
         */
        payCarsOrderFun(e) {
            uni.navigateTo({
                url: `/pages/things/user/order/pay/index?order_id=${e.currentTarget.dataset.id}`
            });
            that.preRrefresh = true;
        },
        /**拨打电话
         * @description: 
         * @param {*} e
         * @return {*}
         */
        getCallFun(e) {
            uni.makePhoneCall({
                phoneNumber: '15626236839'
            });
        },
        /**跳转上传收据
         * @description: 
         * @param {*} e
         * @return {*}
         */
        linkToUploadReceipt(e) {
            that.buttonType = true;
            uni.navigateTo({
                url: `/pages/things/user/order/freight/uploadReceipt/index?order_id=${e.currentTarget.dataset.id}`,
                complete: function(res) {
                    that.buttonType = false;
                }
            });
            that.preRrefresh = true;
        },
        /**抢车单详情
         * @description: 
         * @param {*} e
         * @return {*}
         */
        getFreightOrderInfo(e) {
            let params = {
                grabs_order_id: that.order_id
            };
            getGrabsGoodsOrderDetail_api(params,that).then(res=>{
                that.orderInfo = res;
            }).catch(err=>{
                console.log(err)
            })
        },
        /**图片预览
         * @description: 
         * @param {*} e
         * @return {*}
         */
        previewImage(e) {
            console.log(e);
            // 预览图片
            uni.previewImage({
                urls: [e.currentTarget.dataset.url],
                current: e.currentTarget.dataset.url
            });
        }
    },
    // 页面生命周期
    onLoad: function (options) {
        that = this;
        if (!options.order_id || options.order_id == ''|| options.order_id == 'undefined') {
            that.modal({name:'popLinkDialog',value:{type:true,tipTitle:'提示',tipContent:'该订单不存在',linkUrl:1}});
            return false;
        }
        that.order_id = options.order_id;
    },
    onShow: function() {
         that.getFreightOrderInfo();
    }
}
</script>

<style lang="scss" scoped>
    @import "./index";
</style>